import { mergeParameters } from '../../base/utils';
import React from 'react';
import { IMiniCardText, iMiniCardTextJSON, iConfigItem,unitProps } from '.';
import {
  ComponentsType,
  DataType,
  getMappingType,
  iProperty,
  TagType,
} from '../../base/type';
import iGrapesJSConfig from '../../type';
import IOTMiniCardText from './IOTMiniCardText';
import { checkLabelContainTag } from '../../index';

const grapesJSConfig: iGrapesJSConfig<iMiniCardTextJSON, IMiniCardText> = {
  type: [ComponentsType.BoolType, ComponentsType.EnumType, ComponentsType.NumberType],
  id: 'IOTMiniCardText',
  tag: TagType.IOTMiniCardText,
  access: 'r',
  render: (jsonData, ...args) => {
    const props = mergeParameters(jsonData, ...args);
    const { list } = props;
    var renderList = Array<{ title: string; text: string; label?: string;unit?:string ;unitConvertFlag?: unitProps }>();

    list.forEach((item: iConfigItem) => {
      var valueDef = item.valueDef;
      if (!valueDef) {
        valueDef = {};
      }
      renderList.push({
        title: getShowTitle(valueDef.type, valueDef.specs, item?.functionName ?? '', false),
        text: getShowText(valueDef.type, valueDef.specs,item.label),
        label: item.label,
        unit: item.unit,
        unitConvertFlag:item.unitConvertFlag
      });
    });
    props.list = renderList;
    return <IOTMiniCardText {...props}></IOTMiniCardText>;
  },

  buildData: (mock: boolean = false, property?: iProperty) => {
    if (property === undefined) return;

    const configItem: iConfigItem = {
      id: property.id,
      streamName: property['display-name'],
      functionName: property['display-name'],
      streamId: property.streamName,
      valueDef: property.valuedef,
      label: property.label,
      unit: property?.valuedef.specs.unit,
      unitConvertFlag: {
        hour: property?.valuedef.specs.unit === 'h',
        minute: property?.valuedef.specs.unit === 'min',
        second: property?.valuedef.specs.unit === 's',
      },
    };
    //区分类
    const props: iMiniCardTextJSON = {
      title: property['display-name'],
      streamId: property.streamName,
      type: property?.valuedef?.type,
      disabled: false,
      list: [
        {
          ...configItem,
          // desc: getDesc(configItem),
        },
      ],
    };
    if (mock) {
      props.list = [configItem];
    }
    return props;
  },
  traits: () => {
    return [
      {
        type: 'function-config',
        name: 'list',
        label: '添加配置',
        count: 2,
      },
    ];
  },
};
export default grapesJSConfig;

