const getShowText = (type: string, specs: any,label?: string) => {
  const mappingType = getMappingType(type);
  let returnStr = '- -';
  if (specs) {
    if (mappingType === DataType.EnumType) {
      const key = Object.keys(specs.values)[0];
      if (key) {
        returnStr = specs.values[key];
      }
    } else if (mappingType === DataType.FloatType || mappingType === DataType.IntType) {
      const min = specs['min'];
      const unit = specs['unit'];
      returnStr = `${min}`;
      if (!checkLabelContainTag('7', label ?? '')) {
        returnStr = `${min}${unit ?? ''}`;
      }
    } else if (mappingType === DataType.BoolType) {
      const key = Object.keys(specs)[0];
      if (key) {
        returnStr = specs[key];
      }
    }
  }
  return returnStr;
};
const getShowTitle = (type: string, specs: any, streamName: string, addUnit: Boolean) => {
  const mappingType = getMappingType(type);
  let unit = '';
  if (specs) {
    if (mappingType === DataType.FloatType || mappingType === DataType.IntType) {
      unit = specs['unit'];
    }
  } else {
    if (!streamName || streamName.length == 0) {
      return '功能名称';
    }
  }
  if (addUnit) {
    return `${streamName}${unit ?? ''}`;
  } else {
    return `${streamName}`;
  }
};
