/**
 * 拖拽平台物模型支持的组件列表
 *
 * 处理属性支持的组件
 * 处理组件在右侧展示顺序
 * // 获取来源值 detailType === '1' 是为 小程序接入 否则 是 h5 接入
 * // 获取来源值 appType === '1' 是为 小京鱼APP 否则 是 小家APP
 */
export default ( detailType:string,appType:string,allGrapesJSConfig:boolean = false,func:iFunction) => {
  if (allGrapesJSConfig) {
    return [
      IOTNavBarConfig,
      IOTPowerConfig,
      IOTSwitchConfig,
      IOTCordEntryEnumConfig,
      IOTGridConfig,
      IOTCordEntryNumberConfig,
      IOTIconSliderConfig,
      IOTSliderConfig,
      IOTTimeSliderConfig,
      IOTWeatherConfig,
      IOTPoleSliderConfig,
      IOTNoPoleSliderConfig,
      IOTIconGridConfig,
      IOTExecutePowerConfig,
      IOTConsumablesConfig,
      //以上是控制以下是展示展示组件
      IOTCardMultiTextConfig,
      IOTMiniCardTextConfig,
      IOTImgConfig,
      IOTCustomConfig,
      IOTTimeTaskConfig,
    ];
  }
  let components = Array<any>();
  let mappingType = getComponentsType(func.type) as ComponentsType;
	if (detailType === '1'){
		showInBlock(func, IOTSwitchConfig, components);
    showInBlock(func, IOTPowerConfig, components);
		return components;
	}

	showInBlock(func, IOTSwitchConfig, components);
  showInBlock(func, IOTPowerConfig, components);
  showInBlock(func, IOTGridConfig, components);
  showInBlock(func, IOTCordEntryNumberConfig, components);
	showInBlock(func, IOTSliderConfig, components);
  showInBlock(func, IOTIconSliderConfig, components);
	showInBlock(func, IOTCordEntryEnumConfig, components);
  showInBlock(func, IOTWeatherConfig, components);
  showInBlock(func, IOTIconGridConfig, components);
  showInBlock(func, IOTExecutePowerConfig, components);
  showInBlock(func, IOTConsumablesConfig, components);
  //数值类型
  if (mappingType === ComponentsType.NumberType &&  checkLabelContainTag('7', func.property?.label ?? '')) {
    showInBlock(func, IOTTimeSliderConfig, components);
  }
	showInBlock(func, IOTCardMultiTextConfig, components);
	showInBlock(func, IOTMiniCardTextConfig, components);
	showInBlock(func, IOTImgConfig, components);
	showInBlock(func, IOTPoleSliderConfig,components);
	showInBlock(func, IOTNoPoleSliderConfig,components);
  showInBlock(func, IOTCustomConfig, components);
	showInBlock(func, IOTTimeTaskConfig, components);
  return components;
}

	/**
	 *	检查组件是否展示到拖拽列表中
	 *  判断成立添加到返回数组中，不成立则不添加
	 * @memberof iGrapesJSConfig
	 */
	const showInBlock = (func:iFunction,config:iGrapesJSConfig<any,any>,configs:Array<any>) =>{
		let mappingType = getComponentsType(func.type) as ComponentsType;
		if (config.type.includes(mappingType)) {
			if (func.property){
				if (func.property.access.indexOf(config.access) !== -1) {
          configs.push(config);
        }
			}else{
				configs.push(config);
			}
    }
	}
/**
 *检查label('2,3,7')字符中是否包含特定tag
 *1:耗村类 2:故障类 3:支持别名 4:支持云定时 5:事件型 6:用户数据类 7:时间类
 * @param {string} tag
 * @param {string} label
 * @return {*}
 */
export const checkLabelContainTag = (tag:string,label:string) => {
	if (label == undefined){
		return false;
	}
  let labelContainTag = false;
  label.split(',').forEach((item:string)=>{
    if (item === tag){
      labelContainTag = true;
    }
  })
  return labelContainTag;
}
