import React, { useEffect, useState, useCallback } from 'react';
import Slider from './poleSlider';
import { IOTPoleSliderProps,Iitem,specialObjProps } from "./interface";
import './style/index.less';

const prefixCls = 'iot-pole-slider-out';

const returnText = (items: Iitem[], curValue: string) => {
  let values: string = '';
  items.map((item: Iitem) => {
    if (item.value === curValue) {
      values = item.text
    }
  })
  return values;
}

const IOTPoleSlider: React.FC<IOTPoleSliderProps> = ({
  streamId,
  title,
  streamName,
  subTitle,
  type,
  icon,
  showIconFont,
  disabled = false,
  curValue,
  specialObj,
  items,
  styleType,
  startHandler = (val: any) => { },
  endHandler = (val: any) => { },
  ...rest
}) => {
  const [dataSource, setDataSource] = useState<Iitem[]>(items);
  const [currentValue, setcurrentValue] = useState<string>(curValue);
  const [currentText, setcurrentText] = useState<string>(returnText(items, curValue));
  const defaultDataSource:Iitem[] = JSON.parse(JSON.stringify(items));

  useEffect(() => {
    setcurrentValue(curValue);
    setcurrentText(returnText(items, curValue));
    const dataList: any = [];
    if (specialObj?.checked && specialObj?.item !== undefined) {
      items.map((item: any) => {
        if (item.value !== specialObj?.item?.value) {
          dataList.push(item)
        }
      })
      setDataSource(dataList);
    } else {
      setDataSource(items);
    }
  }, [curValue, items, specialObj])

  const filterHandler = (specialObj?: specialObjProps) => {
    if(!specialObj) return;
    const specialData = JSON.parse(JSON.stringify(specialObj));
    if (!specialData?.checked && specialData?.item !== undefined) {
      specialData.item = undefined;
      return specialData;
    }
    return specialData;
  }

  return (
    <div className={prefixCls} {...rest}>
      <div className="iot-pole-slider">
        <div className="iot-pole-slider-top">
          <div className="iot-pole-slider-left">
            {icon?.show && (
              <div className="iot-pole-slider-icon">
                <span
                  className={[
                    'iot-pole-slider-icon-image',
                    icon?.value ? icon.value : 'insideicon-cu-1',
                  ].join(' ')}
                ></span>
              </div>
            )}
            <div
              className="iot-pole-slider-title"
              style={{ marginLeft: icon?.show ? '0' : '.2rem' }}
            >
              <div className="iot-pole-slider-main">{title}</div>
            </div>
            <div className="line"></div>
            <div className="value-unit">
              {curValue ? specialObj?.item && specialObj?.checked && curValue === specialObj?.item?.value ? specialObj?.item?.text : currentText : dataSource[0].text}
            </div>
          </div>
        </div>

        <div className="iot-pole-slider">
          <Slider
            curValue={currentValue + ''}
            disabled={false}
            styleType={styleType}
            defaultDataSource={defaultDataSource}
            specialObj={filterHandler(specialObj)}
            // 用户勾选了 并且 设置了item
            isSpecial={specialObj?.checked && specialObj?.item !== undefined}
            dotsData={dataSource}
            onchange={(val: any) => {
              const item = {
                streamId,
                value: val,
              };
              setcurrentValue(val);
              endHandler(item);
            }}
            move={(val: any) => {
              setcurrentValue(val.currentValue);
              setcurrentText(val.currentText);
            }}
            start={() => {
              startHandler('start');
            }}
          ></Slider>
        </div>
        <div
          className="disabled-pangle"
          style={{ display: `${disabled ? 'block' : 'none'}` }}
        ></div>
      </div>
    </div>
  );
};

export default IOTPoleSlider;
